
.seller {
  position: absolute;
  top: 174px;
  left: 0;
  width: 100%;
  bottom: 0;
  overflow: hidden;
}
.seller .overview {
  padding: 18px;
  position: relative;
}
.seller .overview .title {
  font-size: 14px;
  color: #07111b;
  line-height: 14px;
}
.seller .overview .desc {
  padding-bottom: 18px;
  padding-top: 8px;
  font-size: 0px;
  position: relative;
}
.seller .overview .desc:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .overview .desc .star {
  display: inline-block;
  margin-right: 8px;
}
.seller .overview .desc .text {
  display: inline-block;
  margin-right: 12px;
  vertical-align: top;
  font-size: 10px;
  color: #4d555d;
  line-height: 18px;
}
.seller .overview .remark {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
}
.seller .overview .remark .block {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-right: 1px solid rgba(7,17,27,0.1);
  text-align: center;
}
.seller .overview .remark .block:last-child {
  border-right: 0;
}
.seller .overview .remark .block h2 {
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
  margin-bottom: 4px;
}
.seller .overview .remark .block .content {
  font-size: 10px;
  line-height: 24px;
  color: #07111b;
}
.seller .overview .remark .block .content .stress {
  font-size: 24px;
  font-weight: 200;
  color: #07111b;
  line-height: 24px;
}
.seller .overview .favorite {
  position: absolute;
  right: 18px;
  top: 10px;
  text-align: center;
}
.seller .overview .favorite .icon {
  padding: 4px;
  display: block;
  margin-bottom: 4px;
  line-height: 24px;
  font-size: 24px;
  color: #d4d6d9;
}
.seller .overview .favorite .icon.active {
  color: #f01414;
}
.seller .overview .favorite .text {
  font-size: 10px;
  color: #4d555d;
  line-height: 20px;
}
.seller .split .line {
  height: 16px;
  width: 100%;
  background-color: #f3f5f7;
  border-top: 1px solid rgba(7,17,27,0.1);
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.seller .split .info {
  padding: 18px 0;
  margin: 0 18px;
