}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .split .list .item .icon.special {
    background-image: url("special_4@3x.png");
}
}
.seller .split .list .item .icon.guarantee {
  background-image: url("guarantee_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .split .list .item .icon.guarantee {
    background-image: url("guarantee_4@3x.png");
}
}
.seller .split .list .item .desc {
  line-height: 16px;
  font-size: 12px;
  font-weight: 200;
  color: #07111b;
}
.seller .pics {
  padding: 18px;
}
.seller .pics .title {
  line-height: 14px;
  margin-bottom: 12px;
  font-size: 14px;
