
.shop-cart {
  position: fixed;
  height: 48px;
  width: 100%;
  left: 0;
  bottom: 0;
  z-index: 50;
}
.shop-cart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #141d27;
  font-size: 0;
}
.shop-cart .content .con-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0px;
}
.shop-cart .content .con-left .logo-wrapper,
.shop-cart .content .con-left .price,
.shop-cart .content .con-left .desc {
  display: inline-block;
}
.shop-cart .content .con-left .logo-wrapper {
  position: relative;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
  background-color: #141d27;
}
.shop-cart .content .con-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #2b343c;
  font-size: 24px;
  line-height: 45px;
  text-align: center;
}
.shop-cart .content .con-left .logo-wrapper .logo.highlight {
  background: #00a0dc;
}
.shop-cart .content .con-left .logo-wrapper .logo .icon-shopping_cart {
  color: rgba(255, 255, 255, 0.4);
  font-size: 24px;
}
.shop-cart .content .con-left .logo-wrapper .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shop-cart .content .con-left .logo-wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 16px;
  font-size: 9px;
  font-weight: 700;
  color: #fff;
  background-color: #f01414;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shop-cart .content .con-left .price {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  line-height: 24px;
  height: 24px;
  padding-right: 12px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  font-size: 16px;
  font-weight: 700;
  color: #7f858a;
}
.shop-cart .content .con-left .price.highlight {
  color: #fff;
}
.shop-cart .content .con-left .desc {
  display: inline-block;
  font-size: 10px;
  line-height: 24px;
  height: 24px;
  color: #7f858a;
  margin-top: 12px;
  text-indent: 12px;
}
.shop-cart .content .con-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
  line-height: 48px;
  height: 48px;
  padding: 0 8px;
  box-sizing: border-box;
  color: #7f858a;
  background-color: #2b343c;
  text-align: center;
}
.shop-cart .content .con-right.enough {
  background-color: #34B413;
}
.shop-cart .content .con-right.enough .pay {
  color: #fff;
  font-size: 16px;
}
.shop-cart .content .con-right .pay {
  font-size: 12px;
  font-weight: 700;
}
.shop-cart .ball-container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  -webkit-transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shop-cart .ball-container .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.shop-cart .shopcart-list {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.shop-cart .shopcart-list.fold-enter-active,
.shop-cart .shopcart-list.fold-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.shop-cart .shopcart-list.fold-enter,
.shop-cart .shopcart-list.fold-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shop-cart .shopcart-list .list-header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background-color: #f3f5f7;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shop-cart .shopcart-list .list-header .title {
  float: left;
  font-size: 14px;
  color: #07111b;
}
.shop-cart .shopcart-list .list-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shop-cart .shopcart-list .list-conent {
  padding: 0 18px;
  max-height: 217px;
  background-color: #fff;
  overflow: hidden;
}
.shop-cart .shopcart-list .list-conent .food {
  position: relative;
  padding: 12px 0;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shop-cart .shopcart-list .list-conent .food .name {
  line-height: 24px;
  font-size: 14px;
  color: #07111b;
}
.shop-cart .shopcart-list .list-conent .food .price {
  position: absolute;
  right: 94px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
}
.shop-cart .shopcart-list .list-conent .food .cartcontroll-wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  opacity: 1;
  background: rgba(7, 17, 27, 0.6);
}
.list-mask.fade-enter-active,
.list-mask.fade-leave-active {
  -webkit-transition: 0.5s all;
  transition: 0.5s all;
}
.list-mask.fade-enter,
.list-mask.fade-leave-active {
  opacity: 0;
  background: rgba(7, 17, 27, 0);
}



