
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background-color: #fff;
  -webkit-transform: translate3d(0, 0, 0) /*food.vue从右平移到左*/;
          transform: translate3d(0, 0, 0) /*food.vue从右平移到左*/;
}
.food.move-enter-active,
.food.move-leave-active {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.food.move-enter,
.food.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .img-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .img-header img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.food .img-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .img-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .content {
  padding: 18px;
  position: relative;
}
.food .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food .content .detail {
  margin-bottom: 10px;
  line-height: 10px;
  font-size: 0px;
  height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  color: #93999f;
  font-size: 10px;
}
.food .content .detail .sell-count {
  margin-right: 12px;
}
.food .content .price .now,
.food .content .price add {
  line-height: 24px;
  font-weight: 700;
}
.food .content .price .now {
  font-size: 14px;
  color: #f01414;
  margin-right: 8px;
}
.food .content .price .odd {
  font-size: 10px;
  color: #93999f;
  text-decoration: line-through;
}
.food .content .cartcontroll-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  text-align: center;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background-color: #00a0dc;
  opacity: 1 /*加入购车车延时消失*/;
}
.food .content .buy.fade-enter-active,
.food .content .buyfade.leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.food .content .buy.fade-enter,
.food .content .buy.fade-leave-active {
  opacity: 0;
  z-index: -1;
}
.food .rating {
  padding-top: 18px;
}
.food .rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: #07111b;
}
.food .rating .rating-wrapper {
  padding: 0 18px;
}
.food .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.food .rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 0;
}
.food .rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  margin-right: 6px;
}
.food .rating .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.food .rating .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up,
.food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  line-height: 16px;
  margin-right: 4px;
  font-size: 12px;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food .rating .no-rating {
  padding: 16px;
  font-size: 12px;
  color: #11999f;
}


