
.split .line {
  height: 16px;
  width: 100%;
  background-color: #f3f5f7;
  border-top: 1px solid rgba(7,17,27,0.1);
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.split .info {
  padding: 18px;
}
.split .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.split .info .text {
  color: #4d555d;
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
}


