
.cartcontroll {
  font-size: 0px;
}
.cartcontroll .cart-decrease {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartcontroll .cart-decrease .inner {
  display: inline-block;
  padding: 6px;
  color: #00A0DC;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.cartcontroll .cart-decrease.move-enter-active,
.cartcontroll .cart-decrease.move-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontroll .cart-decrease.move-enter,
.cartcontroll .cart-decrease.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.cartcontroll .cart-decrease.move-enter .inner,
.cartcontroll .cart-decrease.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontroll .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartcontroll .cart-add {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  padding: 6px;
  color: #00A0DC;
}



